.faq-item {
  border-bottom: 1px solid var(--polosy);
  &:first-child {
    padding-bottom: 4.8rem;
  }
  &:not(:first-child) {
    padding: 4.7rem 0 4.5rem;
  }
  &__head {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    box-sizing: border-box;
    font-size: var(--font-medium);
    line-height: 110%;
    letter-spacing: -0.03em;
    color: var(--osnovnoy-tekst);
    cursor: pointer;
  }
  &__body {
    box-sizing: border-box;
    max-height: 0;
    max-width: 68%;
    opacity: 0;
    overflow: hidden;
    transition: 0.4s;
    line-height: 150%;
    letter-spacing: -0.03em;
    color: var(--osnovnoy-tekst);

    > *:first-child {
      margin-top: 0;
    }
    > *:last-child {
      margin-bottom: 0;
    }
  }
  &__icon {
    width: 28px;
    height: 28px;
    margin-left: 30px;
    transition: 0.4s;
    path {
      stroke-width: 2px;
      fill: var(--chernyy);
    }
  }
  &.js-active {
    .faq-item__body {
      padding-top: 2.5rem;
      max-height: 62.5rem;
      opacity: 1;
    }
    .faq-item__icon {
      transform: rotate(45deg);
      fill: var(--oranzhevyy);
    }
  }
}
